import React, { Component } from "react";
import { Container, Row, Col } from "reactstrap";

//Import Breadcrumb
import Breadcrumbs from '../../components/Common/Breadcrumb';
import { BaseUrl } from "../../helpers/Baseurl"

//Import Components
import MiniWidgets from "./MiniWidgets";
import RevenueAnalytics from "./RevenueAnalytics";
import SalesAnalytics from "./SalesAnalytics";
import LatestTransactions from "./LatestTransactions";

class Dashboard extends Component {
    constructor(props) {
        super(props);
        this.state = {
            jobs : [],
            breadcrumbItems : [],
            reports : []
        }
    }
    componentDidCatch(error) {
        // Changing the state to true
        // if some error occurs
        console.log(error)
      }
    async componentDidMount() {
 
        const requestOptions = {
               method: 'GET',
               headers: { 'Content-Type': 'application/json' },
           };
           await fetch(`${BaseUrl}/api/jobs/jobs/`,requestOptions)
               .then(res => res.json())
               .then(
                   (data) => {

                       let data_length = data.length
                       let bidding = 0
                       let delivered = 0
                       let sold = 0
                       let completed = 0
                       for (let i=0;i<data_length; i++ ){
                           if(data[i].status === "bidding"){
                            bidding = bidding+1 
                           }else if (data[i].status === "delivered"){
                            delivered = delivered +1 
                           }else if (data[i].status === "sold"){
                            sold = sold +1 
                            }else {
                            completed = completed+1
                           }
                       }
                       let reports = [{ icon : "ri-bookmark-3-fill", title : "Number of Bidding Jobs", value : bidding , rate : data_length, desc : "From previous period" },
                        { icon : "ri-store-2-line", title : "Number of Delivered Jobs", value : delivered, rate : data_length, desc : "From previous period" },
                        { icon : "ri-exchange-dollar-fill", title : "Number of Sold Jobs", value : sold, rate : data_length, desc : "From previous period" },
                        { icon : "ri-briefcase-4-line", title : "Number of Completed Jobs", value : completed, rate : data_length, desc : "From previous period" },
                        ]

                   this.setState({  jobs: data, reports:reports })
                   },
                   (error) => {
                       console.log(error, 'error--------------') 
                   }
               )  
     }

    render() {
        const {jobs,reports} = this.state
        return (
            <React.Fragment>
                <div className="page-content">
                    <Container fluid>

                    <Breadcrumbs title="Dashboard" breadcrumbItems={[]} />
                        <Row>
                            <Col xl={12}>
                                <Row>
                                    <MiniWidgets reports={reports} />
                                </Row>
                            </Col>
                            <Col xl={8}>
                            <RevenueAnalytics/>
                            </Col>
                            <Col xl={4}>
                                {/* sales Analytics */}
                                <SalesAnalytics
                                 />
                            </Col>
                        </Row>
                        <Row>
                        
                            {/* latest transactions */}
                            <LatestTransactions
                            jobs = {jobs}/>
                        </Row>

                    </Container> 
                </div>
            </React.Fragment>
        );
    }
}

export default Dashboard;
